$mainfontfamiy:'Noto Serif', serif;

/*-------@media-------------*/
$breakpoint-xl: 1200px !default;
$breakpoint-lg: 1024px !default;
$breakpoint-md: 768px !default;

$breakpoints: (
  'xl' : 'screen and (max-width: 1200px)',
  'lg' : 'screen and (max-width: 1024px)',
  'md' : 'screen and (max-width: 768px)',
) !default;

@mixin mq($breakpoint: md){
  @media #{map-get($breakpoints, $breakpoint)}{
    @content;
  }
}

@include mq(xl) {/* base-size */}
@include mq(lg) {/* Wider than s-size */}
@include mq(md) {/* Wider than m-size */}

body{
  margin: 0;
  padding: 0;
  position: relative;
  font-size: 16px;
  color: #333;
  font-family: 'Noto Serif', serif;
}
/*--------------pageTop-----------------*/
.pagetop{
  display: block;
  position: fixed;
  right: 40px;
  bottom: 60px;
  width: 65px;
  height: 65px;
  line-height: 60px;
  color: #fff;
  border-radius: 50%;
  background-color: rgba(3,3,3,0.55);
  box-shadow: 0 3px 6px rgba(0,0,0,16);
  text-decoration: none;
  font-size: 35px;
  text-align: center;
  transition: .3s;
  @include mq(lg){
    bottom: 100px;
  }
  @include mq(){
    right: 20px;
  }
  &:hover{
    box-shadow:  0 3px 6px rgba(0,0,0,.3);
  }
}
/*------------Bottom Nav--------*/
.btm-nav{
  display: none;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 55px;
  background-color: #fff;
  padding-bottom: env(safe-area-inset-bottom);
  border-top: 1px solid #333;
  @include mq(){
    display: block;
  }
  &__items{
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 75%;
    height: 100%;
    list-style: none;
    margin: 0 auto;
  }
  &__item{
    text-align: center;
    margin-top: 4px;
    a{
      display: inline-block;
      text-decoration: none;
      color: #333;
      width: 70px;
      height: 100%;
      font-size: 20px;
      line-height: 22px;
      margin: 0;
      padding: 0;
    }
    span{
      font-size: 16px;
    }
  }
}
/*---------------header-----------------*/
.header{
  position: fixed;
  top: 0;
  background-color: #fff;
  width: 100%;
  height: 65px;
  display: flex;
  align-items: center;
  z-index: 100;
  &-ttl{
    margin-left: 50px;
    @include mq(){
      margin: 0 auto;
    }
    a{
      font-size: 35px;
      font-weight: 700;
      text-decoration: none;
      color: #333;
      &:hover{
        opacity: 0.5;
      }
      @include mq(){
        text-align: center;
      }
    }
  }
  &-nav{
    width: 480px;
    margin-left: auto;
    margin-right: 35px;
    @include mq(){
      display: none;
    }
    &__list{
      display: flex;
      list-style: none;
    }
    &__item{
      margin-right: 35px;
      &:last-child{
        margin-right: 0;
      }
      a{
        text-decoration: none;
        color: #333;
        font-size: 23px;
        font-weight: 400;
        font-family: $mainfontfamiy;
        &:hover{
          text-decoration: underline;
        }
      }
    }
  }
}
/*-----------------main---------------------*/
.main{
  padding-top: 65px;
  position: relative;
  width: 100%;
  height: 100vh;
  background-image: url(../../dist/img/mainbg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  h1{
    position: absolute;
    font-size: 60px;
    font-weight: 700;
    color: #fff;
    top: 40%;
    left: 10%;
    @include mq(){
      font-size: 45px;
    }
  }
}
/*-------------about-------------*/
.about{
  padding: 65px 0;
  &-container{
    display: flex;
    width: 100%;
    justify-content: center;
    margin-top: 65px;
    @include mq(lg){
      flex-direction: column;
    }
  }
  &__img-area{
    margin-right: 70px;
    @include mq(lg){
      margin-right: 0;
      text-align: center;
    }
    img{
      width: 400px;
      height: 400px;
      border-radius: 50%;
      margin: 15px auto 0;
      @include mq(xl){
        width: 350px;
        height: 350px;
        object-fit: cover;
      }
      @include mq(){
        width: 280px;
        height: 280px;
      }
    }
    a{
      display: block;
      font-size: 20px;
      text-align: center;
      text-decoration: none;
      color: #333;
      margin : 15px auto 0;
      @include mq(lg){
        text-decoration: underline;
      }
      &:hover{
        text-decoration: underline;
      }
    }
    .fa-github{
      font-size: 30px;
      margin-right: 15px;
    }
  }
  &__txtbox{
    width: 700px;
    margin-left: 70px;
    @include mq(lg){
      width: 85%;
      margin: 35px auto 0;
    }
    @include mq(xl){
      width: 500px;
      margin-left: 35px;
    }
  }
  &-txt{
    display: flex;
    @include mq(lg){
      margin: 0 auto;
    }
    &__icon{
      font-size: 30px;
      margin-top: 3px;
      margin-right: 30px;
    }
    &__contents{
      h2{
        font-size: 20px;
        font-weight: 500;
        margin-top: 10px;
        margin-bottom: 25px;
      }
      p{
        margin-bottom: 20px;
        line-height: 30px;
      }
      span{
        color: red;
      }
    }
  }
}
/*----------------Skills-------------*/
.skills{
  padding: 65px 0;
  background-color: #fafafa;
  &-content{
    max-width: 1300px;
    margin: 50px auto 0;
    display: grid;
    justify-content: center;
    justify-items: center;
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
    grid-template-rows: 1fr;
    grid-column-gap: 30px;
    grid-row-gap: 30px;
    display: -ms-grid;
    -ms-grid-columns: 410px 410px 410px;
    -ms-grid-rows: 430px 430px 430px; 
    -webkit-box-pack: center;
    -ms-flex-pack: center; 
    @include mq(){
      grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
    }
    &__box{
      width: 380px;
      height: 400px;
      border: 1px solid #333;
      text-align: center;
      box-shadow: 15px 15px 6px rgba(0,0,0,0.2);
      @include mq(){
        width: 300px;
        height: 320px;
      }
      h2{
        font-size: 30px;
        font-weight: 600;
        margin-top: 60px;
        @include mq(lg){
          margin-top: 15px;
          font-size: 25px;
        }
      }
      i{
        font-size: 70px;
        &.fa-html5{
          color: #E34F26;
        }
        &.fa-css3-alt{
          color: #3996D5;
        }
        &.fa-js-square{
          color: #F8D23D;
        }
        &.fa-wordpress{
          color: #0773AA;
        }
        &.fa-github{
          color: #333;
        }
        &.fa-gulp{
          color: #EB1F21;
        }
        &.fa-adobe{
          color: #FE0100;
        }
      }
      p{
        margin: 30px 50px 0;
        line-height: 30px;
        @include mq(lg){
          margin: 15px 15px 0; 
        }
      }
    }
    &__txt{
      text-align: left;
    }
  }
}

