/*---------------works------------*/
.works{
  padding-top: 65px;
  &__subtitle{
    font-size: 20px;
    font-weight: 500;
    text-align: center;
    margin-top: 35px;
  }
  &-container{
    width: 100%;
    text-align: center;
    margin-top: 65px;
    &__bg{
      background-color: #fafafa;
      padding: 35px 0;
    }
    &__br{
      display: none;
      @include mq(md){
        display: block;
      }
    }
    &__subtitle{
      display: inline-block;
      padding: 0 5px;
      font-size: 20px;
      font-weight: 700;
      border: 1px solid #333;
    }
    h2{
      font-size: 30px;
      font-weight: 700;
      margin-top: 10px;
      @include mq(){
        font-size: 20px;
      }
    }
  }
  &-content{
    display: flex;
    max-width: 1300px;
    margin: 65px auto 0;
    @include mq(xl){
      flex-direction: column;
      margin: 25px auto 0;
    }
    &__inner{
      width: 640px;
      @include mq(){
        width: 100%;
      }
      p{
        width: 500px;
        line-height: 28px;
        @include mq(xl){
          margin-top: 35px;
          width: 100%;
          line-height: 35px;
        }    
      }
      @include mq(xl){
        margin: 0 auto;
      }  
      &__left{
        margin-left: 80px;
        text-align: left;
        @include mq(xl){
          margin: 0 auto;
          width: 55%;
        }    
        @include mq(){
          width: 85%;
        }
      }
    }
    &__txt1{
      margin-bottom: 65px;
      a{
        font-size: 16px;
        font-weight: 400;
        text-decoration: underline;
        color: #3774EE;
        &:hover{
          opacity: 0.5;
        }
      }
    }
    &__Google-D{
      margin-top: 25px;
      a{
        display: inline-block;
        font-size: 20px;
        font-weight: 500;
        color: #3774EE;
        text-decoration: none;
        margin: 0 10px;
        &:last-child{
          margin-right: 0;
        }
        &:hover{
          text-decoration: underline;
        }
      }  
    }
    &__link{
      margin-top: 25px;
      a{
        font-size: 25px;
        font-weight: 500;
        color: #3774EE;
        text-decoration: underline;
        &:hover{
          opacity: 0.5;
        }
      }
    }
    &__detail{
      h4{
        font-size: 20px;
        font-weight: 700;
        margin-top: 30px;
      }
      p{
        margin-top: 15px;
        @include mq(xl){
          margin-top: 20px;
        }
      }
      a{
        display: inline-block;
        margin-top: 15px;
        font-size: 20px;
        font-weight: 600;
        color: #3774EE;
        text-decoration: none;
        &:hover{
          opacity: 0.5;
        }
        @include mq(lg){
          margin-top: 20px;
        }
        .fa-github{
          font-size: 20px;
          font-weight: 400;
          margin-right: 10px;
        }
      }
    }
    &__imgbox1{
      width: 640px;
      height: 550px;
      box-shadow: 10px 10px 5px rgba(0,0,0,0.2);
      @include mq(md){
        width: 330px;
        height: 300px;
        margin: 0 auto;
      }
      img{
        width: 640px;
        height: 550px;
        object-fit: cover;
        @include mq(md){
          width: 330px;
          height: 300px;
        }
      }
    }
    &__imgbox2{
      margin-top: 25px;
      img{
        width: 530px;
        height: 325px;
        @include mq(){
          width: 300px;
          height: 220px;
        }
      }
    }
  }
}
/*-----------contact-------------*/
.contact{
  margin-top: 125px;
  background-color: #fafafa;
  padding: 55px 0;
  z-index: 50;
  @include mq(xl){
    margin-top: 80px;
  }
  &-items{
    margin: 85px auto 0;
    max-width: 1100px;
    @include mq(xl){
      width: 700px;
    }
    @include mq(lg){
      width: 55%;
    }
    @include mq(){
      width: 90%;
      margin: 35px auto 0;
    }
    &__item{
      display: flex;
      margin: 45px auto 0;
      width: 100%;
      @include mq(){
        flex-direction: column;
        margin: 30px auto 0;
      }
    }
    &__title{
      display: inline-block;
      font-size: 20px;
      font-weight: 500;
      text-align: left;
      width: 200px;
      @include mq(){
        font-size: 16px;
        margin: 10px auto 0;
        text-align: center;
      }
    }
    &__required{
      display: inline-block;
      height: 30px;
      width: 70px;
      font-size: 20px;
      font-weight: 500;
      text-align: center;
      color: #fff;
      padding: 0 10px;
      background-color: #F37523;
      text-align: center;
      margin-right: 40px;
      border-radius: 5px;
      @include mq(lg){
        margin-right: 10px;
      }
      @include mq(){
        margin: 0 auto;
      }
    }
    &__input{
      display: inline-block;
      width: 700px;
      height: 35px;
      padding-left: 8px;
      border: 1px solid #333;
      border-radius: 5px;
      @include mq(xl){
        width: 325px;
        margin: 0 auto;
      }
    }
    &__textarea{
      display: inline-block;
      width: 700px;
      height: 170px;
      border: 1px solid #333;
      border-radius: 5px;
      padding: 8px;
      @include mq(xl){
        width: 325px;
        margin: 0 auto;
      }
    }
    &__btn{
      text-align: center;
      input{
        display: inline-block;
        text-align: center;
        margin: 35px auto 0;
        width: 180px;
        height: 50px;
        font-size: 25px;
        font-weight: 500;
        background-color: #F37523;
        border-radius: 5px;
        color: #fff;
        &:hover{
          opacity: 0.5;
        }
      }
    }
  }
}
/*-------------footer------------*/
.footer{
  width: 100%;
  text-align: center;
  @include mq(){
    padding-bottom: 55px;
  }
  small{
    display: block;
    font-size: 14px;
    color: #fff;
    padding: 10px 0;
    background-color: #333;
  }
}


/*-------content-title------------*/
.content-title{
  font-size: 45px;
  font-weight: 700;
  color: #333;
  text-align: center;
  text-shadow: 8px 5px 5px rgba(0,0,0,0.25);
}
